.NameContainer{
    position: absolute;
    z-index: 30;
    top: 77px;
    left: 30px;
    display: flex;
    flex-direction: column;
    padding: 0 30px;
}

.NameContainer h2{
	font-size: 34px;
    letter-spacing: 6px;
    text-transform: uppercase;    
    font-family: Georgia, 'Times New Roman', Times, serif;
}

.NameContainer h2 span{
	display: block;
    font-size: 19px;
    letter-spacing: 5px;
    margin-top: 6px;
}

.NameContainer .border-1, .NameContainer .border-2{
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
    height: 100%;
    z-index: -1;
}

.NameContainer .border-1:before,.NameContainer  .border-1:after,.NameContainer .border-2:before,.NameContainer .border-2:after{
	content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 20px;
    height: 20px;
    opacity: 0.5;
}

.NameContainer .border-1::before{
    border-top: 5px solid #fff;
    border-left: 5px solid #fff;
}
.NameContainer .border-1::after{
    top: auto;
    bottom: 0;
    border-bottom: 5px solid #fff;
    border-left: 5px solid #fff;
}

.NameContainer .border-2::before{
    left: auto;
    right: 0;
    border-top: 5px solid #fff;
    border-right: 5px solid #fff;
}

.NameContainer .border-2::after{
    top: auto;
    bottom: 0;
    left: auto;
    right: 0;
    border-bottom: 5px solid #fff;
    border-right: 5px solid #fff;
}